/*! normalize.css v3.0.2 | MIT License | git.io/normalize */

* {
    box-sizing: border-box
}
*:before,
*:after {
    box-sizing: inherit
}
html, body {
	height:100%;
}
html {
    overflow-y: scroll;
	font-size: 62.5%;
	cursor:default;
    -ms-overflow-style: -ms-autohiding-scrollbar;
    -webkit-text-size-adjust: 100%;
    -ms-text-size-adjust: 100%
	-webkit-tap-highlight-color: rgba(0, 0, 0, 0);
	text-rendering: optimizeLegibility;
	-webkit-font-smoothing :antialiased;
	font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}
body {
    margin: 0
}
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
main,
menu,
nav,
section,
summary {
    display: block
}
audio,
canvas,
progress,
video {
    display: inline-block;
    vertical-align: baseline
}
audio:not([controls]) {
    display: none;
    height: 0
}
[hidden],
template {
    display: none
}
a:active,
a:hover {
    outline: 0
}
abbr[title] {
    border-bottom: 1px dotted
}
b,
strong {
    font-weight: bold
}
dfn {
    font-style: italic
}
h1 {
    font-size: 2em;
    margin: .67em 0
}
small {
    font-size: 80%
}
sub,
sup {
    font-size: 75%;
    line-height: 0;
    position: relative;
    vertical-align: baseline
}
sup {
    top: -0.5em
}
sub {
    bottom: -0.25em
}
svg:not(:root) {
    overflow: hidden
}
figure {
    margin: 1em 40px
}
hr {
    height: 0
}
pre {
    overflow: auto
}
code,
kbd,
pre,
samp {
    font-family: monospace, monospace;
    font-size: 1em
}
button,
input,
optgroup,
select,
textarea {
    font: inherit;
    margin: 0;
    color: inherit
}
button {
    overflow: visible
}
button,
select {
    text-transform: none
}
button,
html input[type='button'],
input[type='reset'],
input[type='submit'] {
    cursor: pointer;
    -webkit-appearance: button
}
button[disabled],
html input[disabled] {
    cursor: default
}
button::-moz-focus-inner,
input::-moz-focus-inner {
    padding: 0;
    border: 0
}
input {
    line-height: normal;
    -webkit-border-radius: 0
}
input[type='checkbox'],
input[type='radio'] {
    padding: 0
}
input[type='number']::-webkit-inner-spin-button,
input[type='number']::-webkit-outer-spin-button {
    width: auto;
    height: auto
}
input[type='search'] {
    -webkit-appearance: textfield
}
input[type='search']::-webkit-search-cancel-button,
input[type='search']::-webkit-search-decoration {
    -webkit-appearance: none
}
fieldset {
    margin: 0 2px;
    padding: .35em .625em .75em;
    border: 1px solid #c0c0c0
}
legend {
    padding: 0;
    border: 0
}
textarea {
    overflow: auto
}
optgroup {
    font-weight: bold
}
table {
    border-spacing: 0;
    border-collapse: collapse
}
td,
th {
    padding: 0
}
[aria-busy='true'] {
    cursor: progress
}
[aria-controls] {
    cursor: pointer
}
[aria-disabled] {
    cursor: default
}
[hidden][aria-hidden='false'] {
    position: absolute;
    display: inherit;
    clip: rect(0 0 0 0)
}
[hidden][aria-hidden='false']:focus {
    clip: auto
}
::-moz-selection {
    color: #fff;
    background: #555;
    text-shadow: none
}
::selection {
    color: #fff;
    background: #555;
    text-shadow: none
}
::-moz-selection {
    color: #fff;
    background: #555;
    text-shadow: none
}
mark {
    color: #fff;
    background: #555
}
img {
    vertical-align: middle;
    border: 0;
    display: inline;
    max-width: 100%;
    height: auto
}
button,
input {
    outline: none
}
:invalid {
    box-shadow: none
}
:-moz-submit-invalid {
    box-shadow: none !important
}
:-moz-ui-invalid {
    box-shadow: none !important
}
::-ms-clear {
    display: none
}
::-ms-reveal {
    display: none
}
select::-ms-value {
    color: currentColor
}
.row {
    margin-left: -15px;
    margin-right: -15px
}
.full {
    margin-right: auto;
    margin-left: auto;
    padding-left: 15px;
    padding-right: 15px
}
@media (min-width:768px){
    .full{
        width:750px;
    }
}
@media (min-width:992px){
    .full{
        width:970px
    }
}
@media (min-width:1200px){
    .full{
        width:1170px
    }
}
hr {
    margin-top: 20px;
    margin-bottom: 20px;
    border: 0;
    border-top: 1px solid #e6e6e6
}
.content ul,
article ul {
    margin: 1em 0;
    padding: 0 0 0 40px;
    list-style-type: disc
}
.hidden,
.hide {
    display: none !important
}
.invisible,
.invis {
    opacity: 0
}
.center,
.cb {
    display: block;
    margin-left: auto;
    margin-right: auto;
    float: none
}
.tc {
    text-align: center
}
.tl {
    text-align: left
}
.tr {
    text-align: right
}
.rel {
    position: relative
}
.abs {
    position: absolute
}
@media print {
    * {
        transition: none !important;
        -webkit-transform: translate3d(0, 0, 0) !important;
        color: #000 !important;
        background: transparent !important;
        box-shadow: none !important;
        text-shadow: none !important
    }
    a,
    a:visited {
        text-decoration: underline !important
    }
    a[href]:after {
        content: ' (' attr(href) ')'
    }
    abbr[title]:after {
        content: ' (' attr(title) ')'
    }
    a[href^='javascript:']:after,
    a[href^='#']:after {
        content: ''
    }
    pre,
    blockquote {
        border: 1px solid #999;
        page-break-inside: avoid
    }
    thead {
        display: table-header-group
    }
    tr,
    img {
        page-break-inside: avoid
    }
    p,
    h2,
    h3 {
        orphans: 3;
        widows: 3
    }
    h2,
    h3 {
        page-break-after: avoid
    }
}